import { Link } from 'react-router-dom'

class Home extends PureComponent {
  state = {
    modalVisible: false
  }
  closeModal = () => {
    this.setState({
      modalVisible: false
    })
  }
  openModal = () => {
    window.ZaloPay.scanARCampaign()
  }

  render() {
    return (
      <div className="page-container">
        <div className="page-content">
          Home
          <Fragment />
          <div className="page-body">
            <span className="page-body-text-name">HEO SỮA</span>
            <div className="page-body-buttons">
              <Button
                variant="contained"
                color="secondary"
                onClick={() => {
                  this.openModal()
                }}
              >
                Săn Heo
              </Button>
              <Link to="/guide">
                <Button variant="contained" color="secondary" href="/guide">
                  Hướng Dẫn
                </Button>
              </Link>
            </div>
            <span className="page-body-text-point">125</span>
            <img
              src={require('../../images/point.png')}
              width={30}
              height={30}
            />
          </div>
        </div>
        <Modal
          visible={this.state.modalVisible}
          width="400"
          height="300"
          // effect="fadeInUp"
          onClickAway={() => this.closeModal()}
        >
          <div>
            <h1>Title</h1>
            <p>Some Contents</p>
            <a href="javascript:void(0);" onClick={() => this.closeModal()}>
              Close
            </a>
          </div>
        </Modal>
      </div>
    )
  }
}

Home.propTypes = {}

const mapStateToProps = () => ({})

const mapDispatchToProps = () => ({})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Home)
